"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { 
  BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer, PieChart, Pie, Cell 
} from "recharts"
import { AlertTriangle, RefreshCw } from "lucide-react"

// Define the assumed structure for the chart data coming from the API
interface OperationHistoryData {
  month: string;
  receipts: number;
  deliveries: number;
}

interface StockCompositionData {
  name: string;
  value: number;
}

// Colors for the Pie Chart
const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];

export default function DashboardPage() {
  const [stats, setStats] = useState({
    total_products: 0,
    low_stock_items: 0,
    pending_receipts: 0,
    pending_deliveries: 0,
    pending_transfers: 0
  })
  
  // State for the chart data
  const [operationsHistory, setOperationsHistory] = useState<OperationHistoryData[]>([]);
  const [stockComposition, setStockComposition] = useState<StockCompositionData[]>([]);

  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null);


  // --- Data Fetching Effect (Runs on mount and repeats every 60s) ---
  useEffect(() => {
    // 1. Check Auth
    const token = localStorage.getItem('token')
    if (!token) {
      window.location.href = "/login" 
      return
    }

    // 2. Fetch All Dashboard Data Concurrently
    const fetchDashboardData = async () => {
      // Only show loading spinner on initial fetch, not during background polling
      if (operationsHistory.length === 0 && stockComposition.length === 0) {
        setLoading(true);
      }
      setError(null);

      try {
        const headers = {
          'Authorization': `Token ${token}`,
          'Content-Type': 'application/json'
        }

        // Use Promise.all to fetch all three required data sets at the same time
        const [statsResponse, historyResponse, distributionResponse] = await Promise.all([
          fetch('http://127.0.0.1:8000/api/dashboard/stats/', { headers }),
          fetch('http://127.0.0.1:8000/api/dashboard/operations-history/', { headers }),
          fetch('http://127.0.0.1:8000/api/dashboard/stock-distribution/', { headers }),
        ])

        // Process Stats/KPIs
        if (statsResponse.ok) {
          setStats(await statsResponse.json())
        } else {
          console.error("Failed to fetch stats:", statsResponse.statusText);
        }

        // Process Operations History (Bar Chart Data)
        if (historyResponse.ok) {
          setOperationsHistory(await historyResponse.json())
        } else {
          console.error("Failed to fetch history:", historyResponse.statusText);
        }

        // Process Stock Distribution (Pie Chart Data)
        if (distributionResponse.ok) {
          setStockComposition(await distributionResponse.json())
        } else {
          console.error("Failed to fetch distribution:", distributionResponse.statusText);
        }
        
      } catch (e) {
        console.error("Network or parsing error:", e)
        setError("Could not connect to the API. Please check the backend server status.")
      } finally {
        setLoading(false)
      }
    }

    fetchDashboardData()
    
    // Set up polling to refresh the dashboard data every 60 seconds
    const intervalId = setInterval(fetchDashboardData, 60000); 
    
    // Cleanup function to clear the interval when the component unmounts
    return () => clearInterval(intervalId);

  }, []) 

  // Custom label function - using 'any' type to avoid Recharts typing issues
  const renderCustomizedLabel = (props: any) => {
    const { name, percent } = props;
    if (typeof percent === 'number') {
      return `${name} (${(percent * 100).toFixed(0)}%)`;
    }
    return name;
  };

  const kpis = [
    { label: "Total Products", value: stats.total_products, icon: "üì¶", color: "text-blue-600" },
    { label: "Low Stock Alerts", value: stats.low_stock_items, icon: "‚ö†Ô∏è", color: "text-red-600" },
    { label: "Pending Receipts", value: stats.pending_receipts, icon: "üì•", color: "text-emerald-600" },
    { label: "Pending Deliveries", value: stats.pending_deliveries, icon: "üì§", color: "text-orange-600" },
    { label: "Internal Transfers", value: stats.pending_transfers, icon: "üîÑ", color: "text-indigo-600" },
  ]
  
  if (loading) return (
    <div className="flex h-screen items-center justify-center bg-gray-50 dark:bg-gray-900">
        <RefreshCw className="w-8 h-8 animate-spin text-blue-600 mr-3" />
        <p className="text-lg font-medium text-gray-700 dark:text-gray-300">Loading Dashboard Data...</p>
    </div>
  )

  return (
    <div className="flex min-h-screen bg-gray-50 dark:bg-gray-900">
      {/* Sidebar would go here */}
      <div className="w-64 bg-gray-800 fixed h-full">
        {/* Sidebar placeholder */}
      </div>

      {/* Main Content */}
      <main className="flex-1 ml-64 p-8 dark:bg-gray-900 dark:text-gray-50">
        <header className="mb-8 flex justify-between items-center">
          <div>
            <h1 className="text-3xl font-bold text-gray-900 dark:text-gray-50">Dashboard</h1>
            <p className="text-gray-500 dark:text-gray-400 mt-1">Welcome back, Manager</p>
          </div>
          <div className="text-sm text-gray-400 dark:text-gray-500 flex items-center gap-2">
            <div className="w-2 h-2 rounded-full bg-green-500 animate-pulse"></div>
            Data Connection: <span className="text-green-500 font-bold">Active</span>
          </div>
        </header>

        {/* Error Message Display */}
        {error && (
            <div className="mb-8 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg flex items-center gap-3 shadow-md dark:bg-red-950 dark:border-red-700 dark:text-red-300">
                <AlertTriangle className="w-5 h-5 flex-shrink-0" />
                <p className="font-medium">{error}</p>
            </div>
        )}

        {/* KPI Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4 mb-8">
          {kpis.map((kpi, index) => (
            <Card key={index} className="border-none shadow-lg bg-white dark:bg-gray-800 hover:shadow-xl transition-shadow duration-300">
              <CardContent className="p-6 flex items-start justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-500 dark:text-gray-400">{kpi.label}</p>
                  <h3 className="text-3xl font-extrabold mt-2">{kpi.value}</h3>
                </div>
                <div className={`text-3xl ${kpi.color}`}>{kpi.icon}</div>
              </CardContent>
            </Card>
          ))}
        </div>

        {/* Charts Section */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          <Card className="lg:col-span-2 border-none shadow-lg bg-white dark:bg-gray-800">
            <CardHeader>
              <CardTitle>Operations Overview</CardTitle>
              <CardDescription>Inbound vs Outbound (Last 4 Months)</CardDescription>
            </CardHeader>
            <CardContent>
              {operationsHistory.length > 0 ? (
                <ResponsiveContainer width="100%" height={300}>
                  <BarChart data={operationsHistory}>
                    <CartesianGrid strokeDasharray="3 3" vertical={false} stroke="#e5e7eb" />
                    <XAxis dataKey="month" stroke="#6b7280" />
                    <YAxis stroke="#6b7280" />
                    <Tooltip 
                        contentStyle={{ backgroundColor: '#1f2937', borderColor: '#4b5563', color: '#f3f4f6', borderRadius: '8px' }}
                        labelStyle={{ color: '#9ca3af' }}
                    />
                    <Legend iconType="circle" wrapperStyle={{ paddingTop: '10px' }}/>
                    <Bar dataKey="receipts" name="Receipts (In)" fill="#10b981" radius={[4, 4, 0, 0]} />
                    <Bar dataKey="deliveries" name="Deliveries (Out)" fill="#f97316" radius={[4, 4, 0, 0]} />
                  </BarChart>
                </ResponsiveContainer>
              ) : (
                <div className="h-64 flex items-center justify-center text-gray-500">
                  No operations history available.
                </div>
              )}
            </CardContent>
          </Card>

          <Card className="border-none shadow-lg bg-white dark:bg-gray-800">
            <CardHeader>
              <CardTitle>Inventory Composition</CardTitle>
              <CardDescription>By Category</CardDescription>
            </CardHeader>
            <CardContent>
              {stockComposition.length > 0 ? (
                <ResponsiveContainer width="100%" height={300}>
                  <PieChart>
                    <Pie 
                      {...{ data: stockComposition }}
                      cx="50%" 
                      cy="50%" 
                      innerRadius={70} 
                      outerRadius={100} 
                      paddingAngle={5} 
                      dataKey="value"
                      labelLine={false}
                      label={renderCustomizedLabel as any}
                    >
                      {stockComposition.map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                      ))}
                    </Pie>
                    <Tooltip 
                        contentStyle={{ backgroundColor: '#1f2937', borderColor: '#4b5563', color: '#f3f4f6', borderRadius: '8px' }}
                        labelStyle={{ color: '#9ca3af' }}
                    />
                    <Legend verticalAlign="bottom" height={36}/>
                  </PieChart>
                </ResponsiveContainer>
              ) : (
                <div className="h-64 flex items-center justify-center text-gray-500">
                  No stock composition data available.
                </div>
              )}
            </CardContent>
          </Card>
        </div>
      </main>
    </div>
  )
}